export default "/*\r\nCSS inspired by Fireship\r\nhttps://www.youtube.com/watch?v=biOMz4puGt8\r\n*/\r\n\r\n.navbar {\r\n  margin: 0;\r\n  padding: 0;\r\n  z-index: 1;\r\n  position: fixed;\r\n  background-color: #23232e;\r\n  transition: width 600ms ease;\r\n  overflow: hidden;\r\n}\r\n\r\n.navbar-nav {\r\n  list-style: none;\r\n  padding: 0;\r\n  margin: 0;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  height: 100%;\r\n}\r\n\r\n.nav-link {\r\n  display: flex;\r\n  align-items: center;\r\n  color: #b6b6b6 !important;\r\n  text-decoration: none;\r\n  -webkit-filter: grayscale(100%) opacity(0.7);\r\n          filter: grayscale(100%) opacity(0.7);\r\n  transition: 600ms;\r\n}\r\n\r\n.nav-link:hover {\r\n  -webkit-filter: grayscale(0%) opacity(1);\r\n          filter: grayscale(0%) opacity(1);\r\n  background: #141418;\r\n  color: #ececec !important;\r\n}\r\n\r\n.link-text {\r\n  display: none;\r\n  margin-left: 1rem;\r\n}\r\n\r\n.nav-link img {\r\n  margin: 0 1.5rem;\r\n  -webkit-filter: invert(.5) sepia(1) saturate(5) hue-rotate(180deg);\r\n          filter: invert(.5) sepia(1) saturate(5) hue-rotate(180deg);\r\n}\r\n\r\n.logo {\r\n  font-weight: bold;\r\n  text-transform: uppercase;\r\n  margin-bottom: 1rem;\r\n  text-align: center;\r\n  color: #ececec;\r\n  background: #141418;\r\n  font-size: 1.5rem;\r\n  letter-spacing: 0.3ch;\r\n  width: 100%;\r\n}\r\n\r\n.logo img {\r\n  transform: rotate(0deg);\r\n  transition: 600ms;\r\n  -webkit-filter: invert(.5) sepia(1) saturate(5) hue-rotate(180deg);\r\n          filter: invert(.5) sepia(1) saturate(5) hue-rotate(180deg);\r\n}\r\n\r\n.logo-text {\r\n  display: inline;\r\n  position: absolute;\r\n  /* left: -999px; */\r\n  transition: 600ms;\r\n}\r\n\r\n.navbar:hover .logo img {\r\n  transform: rotate(360deg);\r\n  -webkit-filter: invert(.5) sepia(1) saturate(5) hue-rotate(180deg);\r\n          filter: invert(.5) sepia(1) saturate(5) hue-rotate(180deg);\r\n}\r\n\r\n/* Small screens */\r\n\r\n@media only screen and (max-width: 600px) {\r\n  .navbar {\r\n    bottom: 0;\r\n    width: 100vw;\r\n    height: calc(100vw / 6);\r\n  }\r\n\r\n  .logo {\r\n    display: none;\r\n  }\r\n\r\n  .navbar-nav {\r\n    flex-direction: row;\r\n  }\r\n\r\n  .nav-item {\r\n    width: calc(100vw / 6);\r\n  }\r\n\r\n  .nav-link {\r\n    height: calc(100vw / 6);\r\n    justify-content: center;\r\n  }\r\n\r\n  .nav-link img {\r\n    width: calc((100vw / 6) - 4rem);\r\n    min-width: 2rem;\r\n  }\r\n\r\n  main {\r\n    margin: 0;\r\n  }\r\n}\r\n\r\n/* Large screens */\r\n\r\n@media only screen and (min-width: 600px) {\r\n  .navbar {\r\n    top: 0;\r\n    width: 5rem;\r\n    height: 100vh;\r\n  }\r\n\r\n  .navbar:hover {\r\n    width: 22rem;\r\n  }\r\n\r\n  .navbar:hover .link-text {\r\n    display: inline;\r\n  }\r\n\r\n  .nav-item {\r\n    width: 100%;\r\n  }\r\n\r\n  .nav-link {\r\n    height: 5rem;\r\n  }\r\n\r\n  .nav-link img {\r\n    width: 2rem;\r\n    min-width: 2rem;\r\n  }\r\n\r\n  /* .navbar:hover .logo img {\r\n      margin-left: 11rem;\r\n    } */\r\n\r\n  .navbar:hover .logo-text {\r\n    left: 0px;\r\n  }\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"